  }

  .founders__item {
    grid-template-columns: 1fr;
    height: auto;
  }
  .founders__item--second .founders__block {
    padding-left: 40px;
    margin-right: auto;
  }
  .founders__item--second .founders-second__block-2 {
    padding-bottom: 40px;
  }
  .founders__text {
    padding-top: 40px;
    padding-left: 40px;
  }
  .founders__name {
    font-size: 36px;
  }
  .founders__desc {
