.header {
  display: flex;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.section {
  margin-bottom: 120px;
  &--1 {
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 100px;
  }
  &--2 {
    margin-top: 160px;
  }
  &--3 {
    position: relative;
    margin-top: 220px;
    padding: 70px 0;
    padding-bottom: 90px;
    background-color: #fbf7f9;
  }
  &--6 {
    margin-bottom: 0!important;
    padding: 120px 0;
    background-color: #fbf7f9;
  }
  &--7 {
    margin-bottom: 0!important;
    padding: 120px 0;
    background-color: #d3a9c3;
  }
  &--8 {
    margin-top: 0!important;
  }
  &--14 {
    margin-bottom: 0!important;
    padding: 120px 0;
    background-image: url('../img/fr-slogan-bg.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    background-color: #d3a9c3;
  }
  &--15 {
    padding: 120px 0;
    background-color: #fbf7f9;
  }
  &--16 {
    position: relative;
  }
  
}

.title-h2 {
  margin-bottom: 30px;
}

.sec-text {
  font-size: 20px;
  line-height: 160%;
  &__one {
    margin-bottom: 40px!important;
  }
}

.logo {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn {
  display: inline-flex;
  padding: 25px 40px;
  font-size: 0.875em;
  font-weight: 600;
  color: inherit;
  border: 1px solid #222;
  border-radius: 5px;
  transition: all .2s ease-in-out;
  &:active, &:hover {
    color: #fff;
    background-color: #D3A9C3;
    border-color: #D3A9C3;
  }
}

.offer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;
  &__img {
    display: inline-flex;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    right: 0;
    width: 45%;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 198px;
      background-color: #D3A9C3;
      z-index: -1;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 220px;
    }
  }
  &__title {
    margin-bottom: 30px;
    font-size: 48px;
    font-weight: 700;
    letter-spacing: .4px;
  }
  &__text {
    font-size: 20px;
    line-height: 160%;
    span {
      font-weight: 700;
    }
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  &__btn {
    margin-top: 30px;
  }
}

.offer-info {
  width: 81%;
}

.privilege {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 130px;
  row-gap: 50px;
  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__img {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  &__text {
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    line-height: 144%;
  }
}

.slogan {
  &__text {
    max-width: 468px;
    width: 100%;
  }
  &__img {
    display: inline-flex;
    position: absolute;
    bottom: 0;
    left: 53%;
  }
}

.slogan-2 {
  &__text {
    font-size: 36px;
    line-height: 1.2;
  }
  &__img {
    left: 56%;
  }
}

.founders {
  &__title {
    margin-bottom: 30px;
  }
  &__item {
    display: grid;
    grid-template-columns: 380px 1fr 1fr;
    align-items: center;
    height: 414px;
    color: #fff;
    background-color: #d3a9c3;
    background-image: url('../img/founder-1.png');
    background-repeat: no-repeat;
    background-position: left bottom;
    background-position-x: 16px;
    border-radius: 20px;
    &:last-child {
      margin-top: 30px;
    }
    &--second {
      grid-template-columns: 1fr 1fr 330px;
      background-image: url('../img/founder-2.png');
      background-position-x: 16px;
      background-position: right bottom;
      .founders__text {
        margin-left: auto;
      }
    }
  }
  &__block {
    display: flex;
    height: 100%;
    img {
      display: inline-flex;
      align-items: center;
      margin-left: auto;
    }
    &:last-child {
      padding-right: 40px;
    }
  }
  &__text {
    padding-top: 100px;
  }
  &__name {
    margin-bottom: 12px;
    font-size: 48px;
    font-weight: 600;
  }
  &__desc {
    max-width: 226px;
    font-size: 20px;
    line-height: 150%;
    color: rgba(255, 255, 255, .8);
  }
}

.partners__item {
  background-image: url('../img/partners-1.png');
}

.intent {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 260px;
  }
  &__img {
    margin-bottom: 20px;
  }
  &__text {
    font-weight: 500;
    text-align: center;
    line-height: 137%;
  }
}

.details {
  text-align: center;
  &__title {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    text-align: center;
  }
  &__desc {
    font-size: 20px;
    color: rgba(255, 255, 255, .8);
    text-align: center;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
    margin-top: 60px;
    margin-bottom: 40px;
  }
  &__item {
    position: relative;
    height: 360px;
    border-radius: 16px;
    overflow: hidden;
    background-image: url('../img/details-1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    &--2 {
      background-image: url('../img/details-2.jpg');
    }
    &--3 {
      background-image: url('../img/details-3.jpg');
    }
  }
  &__info {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 76px;
    padding: 15px 40px;
    font-size: 18px;
    line-height: 1.2;
    background-color: #fff;
  }
  &__btn {
    background-color: #fff;
    border: none;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.1));
  }  p {
    margin-bottom: 40px;
  }  p {
    margin-bottom: 40px;
  }
}

.templ-block {
  p {
    margin-bottom: 20px;
  }
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
  }
  &__img {
    border-radius: 16px;
  }
  &__item {
    display: inline-flex;
    flex-direction: column;
    img {
      margin-bottom: 0;
      border-radius: 16px;
    }
  }
  &__img {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.slogan-3 {
  color: #fff;
  &__text {
    max-width: 840px;
    font-size: 36px;
    line-height: 1.2;
  }
}

.lump-sum {
  &__title {
    margin-bottom: 80px;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 40px;
  }
  &__item {
    padding: 20px 0;
    background-color: #fff;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    .privilege__text {
      padding: 0 15px;
    }
  }
  &__name {
    margin-bottom: 40px;
    font-size: 36px;
    text-transform: uppercase;
  }
  &__block {
    &:not(:last-child) {
      margin-bottom: 60px;
    }
  }
}

.chapter {
  &__title {
    width: 60%;
    margin-bottom: 60px;
    margin-left: auto;
    font-feature-settings: 'lnum' 1;
  }
  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    row-gap: 30px;
    width: 60%;
    margin-left: auto;
  }
  &__item {
    display: grid;
    grid-template-columns: 58px 1fr;
    align-items: center;
    column-gap: 20px;
  }
  &__num {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 58px;
    height: 58px;
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    background-color: #D3A9C3;
    border-radius: 50%;
  }
  &__text {
    font-weight: 500;
  }
  &__img {
    display: inline-flex;
    position: absolute;
    left: 0;
    bottom: -120px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.footer {
  position: relative;
  padding: 120px 0;
  background-color: #D3A9C3;
  .offer__title {
    color: #fff;
  }
  .offer__text {
    color: #fff;
  }
  &__btn {
    color: #222;
    background-color: #fff;
    border-color: #fff;
    &:hover {
      color: #222;
      background-color: transparent;
      border-color: #222;
    }
  }
  &__img {
    display: inline-flex;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    left: 45%;
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding-bottom: 20px;
  background: rgba(0, 0, 0, 0.8);
  visibility: hidden;
  opacity: 0;
  transition: all .15s ease-in-out;
  &--visible {
    visibility: visible;
    opacity: 1;
  }
}

.modal {
  max-width: 640px;
  margin: 20px;
  background-color: #fff;
  border-radius: 16px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #6969dd #e0e0e0;
  height: calc(100% - 20px);
  display: none;
  &::-webkit-scrollbar {
    width: 6px;
  }
  &__head {
    padding: 20px;
    h2 {
      font-family: 'Montserrat', sans-serif;
      font-size: 36px;
      font-weight: 600;
      text-align: center;
      text-transform: none;
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 60px;
    display: inline-flex;
    width: 40px;
    height: 40px;
    z-index: 102;
    cursor: pointer;
    opacity: 1;
    transition: opacity .2s ease-in-out;
    &:hover {
      opacity: .8;
    }
    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &--visible {
    display: block;
  }
}

.modal-form {
  display: grid;
  grid-template-rows: auto;
  gap: 20px;
  padding-bottom: 20px;
  &__field {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
    padding: 20px;
    background-color: #fff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.1));
    border-radius: 10px;
  }
  &__name {
    margin-bottom: 16px;
    font-size: 16px;
    font-weight: 500;
    span {
      color: #d93025;
    }
  }
  &__input {
    height: 40px;
    font-size: 14px;
    border: none;
    outline: none;
    border-bottom:  1px solid rgba(0,0,0,0.1);
    &::placeholder {
      color: #70757a;
    }
  }
  &__checkbox {
    display: flex;
    align-items: center;
    cursor: pointer;
    &--name {
      margin-left: 8px;
    }
  }
  &__btn {
    width: max-content;
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    color: #fff;
    background-color: #D3A9C3;
    border: none;
    &:hover {
      opacity: .8;
    }
  }
}

.body-hidden {
  overflow: hidden;
}