@media (max-width: (1400px)) {
  .chapter__img {
    right: 0;
    width: 540px;
    height: 540px;
  }
  .footer__img {
    width: 640px;
    height: auto;
  }
}

@media (max-width: (1200px)) {
  .container {
    max-width: 960px;
  }
  .section {
    margin-bottom: 100px;
    &--3 {
      margin-top: 120px;
    }
    &--14 {
      padding: 100px 0;
    }
    &--15 {
      padding: 100px 0;
    }
  }
  h2 {
    font-size: 40px;
  }
  .sec-text {
    font-size: 18px;
  }
  .offer-info {
    width: 100%;
  }
  .offer {
    &__text {
      font-size: 18px;
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
  .privilege {
    gap: 30px;
  }
  .founders {
    &__item {
      grid-template-columns: 1fr;
      height: auto;
      &--second {
        .founders__block {
          padding-left: 40px;
          margin-right: auto;
        }
        .founders-second__block-2 {
          padding-bottom: 40px;
        }
      }
    }
    &__text {
      padding-top: 40px;
      padding-left: 40px;
    }
    &__name {
      font-size: 36px;
    }
    &__desc {
      font-size: 18px;
    }
  }
  .intent {
    gap: 30px;
  }
  .details {
    &__list {
      gap: 20px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    &__item {
      height: 320px;
    }
    &__info {
      height: 60px;
      padding: 10px 15px;
      font-size: 16px;
    }
  }
  .slogan-2 {
    &__img {
      width: 360px;
      height: 360px;
    }
  }
  .lump-sum {
    &__title {
      margin-bottom: 60px;
    }
    &__name {
      font-size: 32px;
    }
    &__list {
      gap: 20px;
    }
  }
  .privilege {
    &__img {
      width: 64px;
      height: 64px;
    }
    &__text {
      font-size: 16px;
    }
  }
  .chapter {
    &__list {
      grid-template-columns: 1fr;
      gap: 20px;
    }
    &__item {
      grid-template-columns: 40px 1fr;
      column-gap: 10px;
    }
    &__num {
      width: 40px;
      height: 40px;
      font-size: 14px;
    }
    &__img {
      bottom: -100px;
      transform: none;
    }
  } 
  .footer {
    padding: 100px 0;
    &__img {
      left: 48%;
      width: 500px;
    }
  }
}

@media (max-width: (1000px)) {
  .container {
    max-width: 100%;
    padding-left: 30px;
    padding-right: 30px;
  }
  h2 {
    font-size: 36px;
  }
  .title-h2 {
    margin-bottom: 20px;
  }
  .sec-text {
    font-size: 16px;
    &__one {
      margin-bottom: 20px!important;
    }
  }
  .section {
    &--1 {
      margin-top: 70px;
    }
    &--14 {
      padding: 70px 0;
    }
  }
  .offer {
    &__title {
      font-size: 40px;
    }
    &__text {
      font-size: 16px;
    }
  }
  .logo {
    width: 130px;
    height: 56px;
  }
  .intent {
    grid-template-columns: 1fr 1fr;
    &__item {
      max-width: 100%;
    }
  }
  .details {
    &__desc {
      font-size: 18px;
    }
    &__list {
      grid-template-columns: 1fr 1fr;
    }
    &__item {
      &--3 {
        grid-column: span 2;
      }
    }
  }
  .slogan-2 {
    position: relative;
    z-index: 1;
    text-align: center;
    background-color: rgba(255, 255, 255, .9);
    border-radius: 16px;
    padding-top: 20px;
    padding-bottom: 20px;
    &__text {
      max-width: 100%;
      font-weight: 500;
    }
    &__img {
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .templ-block {
    &__grid {
      gap: 20px;
    }
  }
  .lump-sum {
    &__list {
      grid-template-columns: 1fr 1fr;
    }
  }
  .chapter {
    position: relative;
    z-index: 1;
    &__title {
      width: 100%;
    }
    &__list {
      width: 100%;
    }
    &__img {
      display: none;
    }
  }
  .footer {
    background-color: #FBF7F9;
    .offer {
      position: relative;
      z-index: 1;
      grid-template-columns: 1fr;
      padding: 40px;
      text-align: center;
      background-color: rgba(255, 255, 255, .9);
      &__title {
        color: #222;
      }
      &__text {
        font-weight: 500;
        color: #222;
      }
    }
    &__img {
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &__btn {
      color: #fff;
      background-color: #D3A9C3;
    }
  }
  .modal {
    &__head {
      h2 {
        max-width: 320px;
        margin-left: auto;
        margin-right: auto;
        font-size: 28px;
      }
    }
    &__close {
      top: 40px;
    }
  }
  .modal-form {
    &__name {
      font-size: 14px;
    }
  }
}

@media (max-width: (700px)) {
  h2 {
    text-align: center;
  }
  .section {
    margin-bottom: 80px;
    &--1 {
      .container {
        position: relative;
        z-index: 1;
      }
    }
    &--2 {
      margin-top: 100px;
    }
    &--3 {
      margin-top: 80px;
      padding-top: 60px;
      padding-bottom: 60px;
    }
    &--6 {
      padding: 80px 0;
    }
    &--7 {
      padding: 80px 0;
    }
  }
  .offer {
    grid-template-columns: 1fr;
    &__img {
      display: none;
    }
    &__text {
      padding-left: 40px;
      padding-right: 40px;
    }
  }
  .offer-info {
    text-align: center;
  }
  .privilege {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    &__img {
      margin-bottom: 15px;
    }
  }
  .slogan {
    position: relative;
    z-index: 1;
    padding: 20px 40px;
    text-align: center;
    background-color: rgba(255, 255, 255, .9);
    border-radius: 50px;
    &__text {
      max-width: 100%;
      font-weight: 500;
    }
    &__img {
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .founders {
    &__item {
      display: flex;
      height: 420px;
      background-size: 240px;
      background-position-x: center;
    }
    &__name {
      font-size: 28px;
    }
    &__desc {
      font-size: 14px;
    }
    &__block {
      &:last-child {
        display: none;
      }
    }
  }
  .founders-second__block-1 {
    display: none;
  }
  .founders__item--second {
    .founders__block {
      padding-left: 0;
    }
  }
  .details {
    &__list {
      grid-template-columns: 1fr;
    }
    &__item {
      &--3 {
        grid-column: span 1;
      }
    }
  }
  .templ-block {
    &__grid {
      grid-template-columns: 1fr;
    }
  }
  .slogan-3 {
    padding: 0;
    background-color: transparent;
  }
  .footer {
    &__img {
      width: 350px;
    }
  }
  .modal {
    margin-top: 15px;
    margin-left: 15px;
    margin-right: 15px;
    &__close {
      top: -2px;
      right: 0;
    }
  }
  .modal-form {
    gap: 15px;
    &__field {
      margin-left: 15px;
      margin-right: 15px;
    }
    &__btn {
      margin-left: 15px;
      margin-right: 15px;
    }
  }
}

@media (max-width: (500px)) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
  h2 {
    font-size: 32px;
    margin-bottom: 20px;
  }
  .sec-text {
    font-size: 14px;
  }
  .section {
    &--1 {
      margin-top: 40px;
    }
    &--2 {
      margin-top: 80px;
    }
    &--14 {
      padding: 40px 0;
    }
    &--15 {
      padding: 80px 0;
    }
  }
  .offer {
    &__title {
      margin-bottom: 20px;
    }
    &__btn {
      margin-top: 20px;
    }
  }
  .privilege {
    gap: 15px;
    &__text {
      font-size: 14px;
    }
  }
  .intent {
    grid-template-columns: 1fr;
    gap: 20px;
    &__text {
      padding-left: 40px;
      padding-right: 40px;
      font-size: 14px;
    }
  }
  .details {
    &__desc {
      font-size: 14px;
    }
  }
  .slogan-2 {
    &__img {
      width: 300px;
      height: 300px;
    }
  }
  .lump-sum {
    &__list {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__name {
      font-size: 26px;
      margin-bottom: 20px;
    }
    .privilege__img {
      width: 50px;
      height: 50px;
    }
  }
  .founders {
    &__text {
      padding: 30px;
    }
  }
  .footer {
    &__img {
      display: none;
    }
    .offer {
      padding: 20px;
    }
  }
  .modal {
    &__head {
      h2 {
        font-size: 24px;
      }
    }
  }
}